<template>
  <!-- 非标准档案导入组件 -->
  <div class="EasyStarImport">
    <el-dialog :visible="visible" @close="closeEditor" title="数据导入" style="padding: 10px;" width="400px" tooltip="数据导入">
      <el-form label-width="0px" size="mini">
        <el-row style="margin-bottom: 10px">
          <el-col>
            <el-upload
              class="upload-demo"
              ref="upload"
              :multiple="false"
              :show-file-list="false"
              accept=".xls,.xlsx,.mdb,.accdb"
              :action = "this.api + this.easyStarAPI.FileUpload"
              :auto-upload="true"
              :on-success="onSuccess">
              <el-button slot="trigger" size="mini" type="primary">选取文件</el-button>
              <el-button style="margin-left: 10px;" size="mini" type="success" @click="startImport">开始导入</el-button>
              <div slot="tip" class="el-upload__tip">只能上传xls/xlsx/mdb/accdb文件，且不超过10MB</div>
            </el-upload>
          </el-col>
        </el-row>
        <el-row style="margin-bottom: 10px">
          <el-col>
            <el-form-item label-width="40px" label="主键" style="margin-bottom: 0px !important; margin-right: 10px;">
              <el-select clearable placeholder="请选择" v-model="keyFieldName">
                <el-option value=""/>
                <el-option v-for="field in sourceFieldList" :key="field" :label="field" :value="field"/>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col>
            <el-table size="mini" :data="targetFieldList" stripe border height="350"
              :header-cell-style="tableHeaderColor"
              @selection-change="handleSelectionChange">
              <el-table-column type="selection" width="35"/>
              <el-table-column label="目标字段" prop="DISSTR"/>
              <el-table-column label="来源字段" prop="target">
                <template slot-scope="scope">
                  <el-form-item label="" style="margin-bottom: 0px !important;" size="mini">
                    <el-select clearable placeholder="请选择" v-model="scope.row.target" @change="checkField">
                      <el-option value=""/>
                      <el-option v-for="field in sourceFieldList" :key="field" :label="field" :value="field"/>
                    </el-select>
                  </el-form-item>
                </template>
              </el-table-column>
            </el-table>
          </el-col>
        </el-row>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'EasyStarImport',
  data () {
    return {
      keyFieldName: '',
      visible: false,
      targetFieldList: [],
      sourceFieldList: [],
      multipleSelection: []
    }
  },
  components: {
  },
  methods: {
    // 修改table header的背景色
    tableHeaderColor ({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 0) {
        return 'color: #000; text-align: center;'
      }
    },
    // 关闭对话框
    closeEditor () {
      this.visible = false
      this.keyFieldName = ''
      this.targetFieldList = []
      this.sourceFieldList = []
      this.multipleSelection = []
    },
    // 加载列
    loadCols (cols) {
      this.targetFieldList = JSON.parse(JSON.stringify(cols))
      console.log(this.targetFieldList)
    },
    // 文件上传成功
    onSuccess (response, file, fileList) {
      if (response.result === 0) {
        this.sourceFieldList = response.files[0].fields
        this.setAssField()
      }
    },
    // 开始导入
    startImport () {
      var jsonSend = {}
      jsonSend.keyfield = this.keyFieldName
      jsonSend.assfields = {}
      for (let index in this.multipleSelection) {
        let item = this.targetFieldList[index]
        if (item.target) {
          jsonSend.assfields[item.FIELDNAME] = item.target
        } else {
          jsonSend.assfields[item.FIELDNAME] = ''
        }
      }
      this.axios.post(this.api + this.easyStarAPI.StartImport,
        jsonSend
      ).then((res) => {
        if (res.data.code === 0) {
          // 抛出上传成功的事件
          this.$emit('importSuccess')
        }
      })
    },
    // 勾选要导入的列
    handleSelectionChange (val) {
      this.multipleSelection = val
    },
    checkField () {
    },
    // 自动匹配对应关系
    setAssField () {
      for (let s1 in this.targetFieldList) {
        for (let s3 in this.sourceFieldList) {
          if (this.targetFieldList[s1].DISSTR === this.sourceFieldList[s3]) {
            // console.log(s1.DISSTR, s3)
            this.targetFieldList[s1].target = this.sourceFieldList[s3]
            break
          }
        }
      }
    }
  }
}
</script>

<style scoped>
.filesView >>> .el-scrollbar__wrap{
  overflow-x: hidden;
}
.headerClass {
  color: #000;
}
</style>



